'use strict';
/**
 * @api PAGE_UNLOAD(function) PAGE_UNLOAD
 * @apiGroup APP
 * @apiVersion 1.0.0
 * @apiParam {function} function 回调函数
 * @apiDescription 界面退出时触发
 * @apiUse APPNpm
 * @apiUse APPJs
 * @apiParamExample {string} 例子
 * // 主要使用场景
 * // 1、app 处于 h5 界面 ios 点 home 键返回桌面 触发 console.log(1)
 * // 2、h5 通过 JUMP_TO 打开另一个 h5 容器 触发 console.log(1)
 * PAGE_UNLOAD(_=>{
 *  console.log(1)
 * })
 * @apiParamExample {string} 返回值
 * 无
 **/

// 页面 销毁

/**
 * todo GLOBAL
 */
import {WinJSDec, fnError} from './APP/config';

var name = 'PAGE_UNLOAD';
var PAGE_UNLOAD = fn => {
  if (typeof fn === 'function') {
    WinJSDec(_ => {
      document.addEventListener('onunload', fn)
    })
  } else {
    throw new TypeError(`[${name}] ${fnError}`);
  }
};
export default PAGE_UNLOAD;